.words {
  display: flex;
  justify-content: center;
  margin-top: 60px;
  margin-left: 30px;
  margin-right: 30px;

  @media only screen and (max-width: 1000px) {
    margin-left: 20px;
    margin-right: 20px;
  }

  &__heading {
    font-size: 2.5rem;
  }

}

  .question {
    display: flex;
    justify-content: center;
    font-size: 1.2rem;
    font-weight: 600;
    margin-top: 10px;
  }

  .answers {
    margin-top: 10px;
    margin-bottom: 20px;

    label {
      display: block;
      border: solid 2px grey;
      border-radius: 20px;
      padding-top: 5px;
      padding-left: 30px;
      padding-right: 30px;
      padding-bottom: 5px;
      margin-bottom: 10px;
      cursor: pointer; 
  
      @media only screen and (min-width: 1000px) {
        padding-top: 5px;
      } 
    }
  }
  
  .slide {
    margin-top: 300px;
    padding-left: 400px;
    padding-right: 400px;
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    z-index: 1;
    opacity: 0;
    transition: opacity 0.5s;

    @media only screen and (max-width: 600px) {
      padding-left: 20px;
      padding-right: 20px;
    } 

    @media only screen and (max-width: 700px) {
      padding-left: 20px;
      padding-right: 20px;
    } 

    @media only screen and (min-width: 700px) {
      padding-left: 200px;
      padding-right: 200px;
    } 

    
    @media only screen and (min-width: 1000px) {
      padding-left: 300px;
      padding-right: 300px;
    } 

    @media only screen and (min-width: 1099px) {
      padding-left: 500px;
      padding-right: 500px;
    } 
  }

  .active-slide{
    opacity: 1;
    z-index: 2;
  }


.buttonsContainer {
  display: flex;
  justify-content: center;
  margin-top: 300px;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 100px;
}

.previousBtn {
  font-family: sans-serif;
  font-size: 1rem;
  background-color: #527590;
  color: #eeeeea;
  border: 0px;
  border-radius: 45px;
  padding-top: 7px;
  padding-right: 13px;
  padding-bottom: 7px;
  padding-left: 13px;
  cursor: pointer;
  margin-bottom: 20px;
  margin-right: 20px;
}

.previousBtn:hover {
  background-color: #cddcdf;
  color: #527590;
}

.nextBtn {
  font-family: sans-serif;
  font-size: 1rem;
  background-color: #527590;
  color: #eeeeea;
  border: 0px;
  border-radius: 45px;
  padding-top: 7px;
  padding-right: 13px;
  padding-bottom: 7px;
  padding-left: 13px;
  cursor: pointer;
  margin-bottom: 20px;
}

  .nextBtn:hover {
    background-color: #cddcdf;
    color: #527590;
  }

.submitBtn {
  font-family: sans-serif;
  font-size: 1rem;
  background-color: #527590;
  color: #eeeeea;
  border: 0px;
  border-radius: 45px;
  padding-top: 7px;
  padding-right: 13px;
  padding-bottom: 7px;
  padding-left: 13px;
  cursor: pointer;
  margin-bottom: 20px;
}

.submitBtn:hover {
  background-color: #cddcdf;
  color: #527590;
}

.result {
  display: flex;
  justify-content: center;
  margin-top: 60px;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 1.2rem;
}